module.exports = function (hljs) {
  var VARIABLE = {
    className: 'variable',
    begin: /\$\(/,
    end: /\)/,
    contains: [hljs.BACKSLASH_ESCAPE]
  };
  return {
    aliases: ['mk', 'mak'],
    contains: [hljs.HASH_COMMENT_MODE, {
      begin: /^\w+\s*\W*=/,
      returnBegin: true,
      relevance: 0,
      starts: {
        className: 'constant',
        end: /\s*\W*=/,
        excludeEnd: true,
        starts: {
          end: /$/,
          relevance: 0,
          contains: [VARIABLE]
        }
      }
    }, {
      className: 'title',
      begin: /^[\w]+:\s*$/
    }, {
      className: 'phony',
      begin: /^\.PHONY:/,
      end: /$/,
      keywords: '.PHONY',
      lexemes: /[\.\w]+/
    }, {
      begin: /^\t+/,
      end: /$/,
      relevance: 0,
      contains: [hljs.QUOTE_STRING_MODE, VARIABLE]
    }]
  };
};