module.exports = function (hljs) {
  var COMMENT_MODES = [hljs.COMMENT('--', '$'), hljs.COMMENT('{-', '-}', {
    contains: ['self']
  })];
  var PRAGMA = {
    className: 'pragma',
    begin: '{-#',
    end: '#-}'
  };
  var PREPROCESSOR = {
    className: 'preprocessor',
    begin: '^#',
    end: '$'
  };
  var CONSTRUCTOR = {
    className: 'type',
    begin: '\\b[A-Z][\\w\']*',
    // TODO: other constructors (build-in, infix).
    relevance: 0
  };
  var LIST = {
    className: 'container',
    begin: '\\(',
    end: '\\)',
    illegal: '"',
    contains: [PRAGMA, PREPROCESSOR, {
      className: 'type',
      begin: '\\b[A-Z][\\w]*(\\((\\.\\.|,|\\w+)\\))?'
    }, hljs.inherit(hljs.TITLE_MODE, {
      begin: '[_a-z][\\w\']*'
    })].concat(COMMENT_MODES)
  };
  var RECORD = {
    className: 'container',
    begin: '{',
    end: '}',
    contains: LIST.contains
  };
  return {
    aliases: ['hs'],
    keywords: 'let in if then else case of where do module import hiding ' + 'qualified type data newtype deriving class instance as default ' + 'infix infixl infixr foreign export ccall stdcall cplusplus ' + 'jvm dotnet safe unsafe family forall mdo proc rec',
    contains: [// Top-level constructions.
    {
      className: 'module',
      begin: '\\bmodule\\b',
      end: 'where',
      keywords: 'module where',
      contains: [LIST].concat(COMMENT_MODES),
      illegal: '\\W\\.|;'
    }, {
      className: 'import',
      begin: '\\bimport\\b',
      end: '$',
      keywords: 'import|0 qualified as hiding',
      contains: [LIST].concat(COMMENT_MODES),
      illegal: '\\W\\.|;'
    }, {
      className: 'class',
      begin: '^(\\s*)?(class|instance)\\b',
      end: 'where',
      keywords: 'class family instance where',
      contains: [CONSTRUCTOR, LIST].concat(COMMENT_MODES)
    }, {
      className: 'typedef',
      begin: '\\b(data|(new)?type)\\b',
      end: '$',
      keywords: 'data family type newtype deriving',
      contains: [PRAGMA, CONSTRUCTOR, LIST, RECORD].concat(COMMENT_MODES)
    }, {
      className: 'default',
      beginKeywords: 'default',
      end: '$',
      contains: [CONSTRUCTOR, LIST].concat(COMMENT_MODES)
    }, {
      className: 'infix',
      beginKeywords: 'infix infixl infixr',
      end: '$',
      contains: [hljs.C_NUMBER_MODE].concat(COMMENT_MODES)
    }, {
      className: 'foreign',
      begin: '\\bforeign\\b',
      end: '$',
      keywords: 'foreign import export ccall stdcall cplusplus jvm ' + 'dotnet safe unsafe',
      contains: [CONSTRUCTOR, hljs.QUOTE_STRING_MODE].concat(COMMENT_MODES)
    }, {
      className: 'shebang',
      begin: '#!\\/usr\\/bin\\/env\ runhaskell',
      end: '$'
    }, // "Whitespaces".
    PRAGMA, PREPROCESSOR, // Literals and names.
    // TODO: characters.
    hljs.QUOTE_STRING_MODE, hljs.C_NUMBER_MODE, CONSTRUCTOR, hljs.inherit(hljs.TITLE_MODE, {
      begin: '^[_a-z][\\w\']*'
    }), {
      begin: '->|<-'
    } // No markup, relevance booster
    ].concat(COMMENT_MODES)
  };
};